import React from "react"
import dayjs from "dayjs"
import { graphql, PageProps, useStaticQuery } from "gatsby"
import useAxios from "axios-hooks"
import { css } from "@emotion/react"
import { MDRender } from "../components/mdRender"
import Layout from "../components/layout"
import VideoPlayer from "../components/video-player"

const ThinkPage: React.FC<PageProps<{ site: any; allMarkdownRemark: any }>> = (
  props
) => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  )
  if (typeof window !== "undefined") {
    const [{ data, loading, error }] = useAxios(
      "/content/videos?_sort=updated_at:DESC"
    )
    console.log(data, loading, error)
    if (!data) {
      return (
        <Layout location={props.location} title={site.siteMetadata.title}>
          {error?.message}
        </Layout>
      )
    }
    return (
      <Layout location={props.location} title={site.siteMetadata.title}>
        <section id="projects">
          {loading ? (
            <span className="dotting">...</span>
          ) : (
            <ul className="project-list">
              {data.map((item: any, index: number) => {
                return (
                  <li
                    css={css`
                      span {
                        color: #2bbc8a;
                      }

                      p {
                        margin-top: 0;
                      }

                      .video-wrap {
                        position: relative;
                        width: 100%;
                        height: 300px;
                        margin-top: 5px;
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        .vjs-big-play-button {
                          top: 50%;
                          left: 50%;
                          transform: translate(-50%, -50%);
                        }
                      }
                    `}
                    className="project-item"
                    key={item.id}
                  >
                    {index !== 0 && (
                      <div
                        css={css`
                          margin: 12px 0;
                        `}
                      />
                    )}
                    <span>
                      {dayjs(item.updated_at).format(`YYYY-MM-DD HH:mm`)}
                    </span>
                    {item.desc && <MDRender md={item.desc} />}
                    {item.url.map((video) => {
                      return (
                        <div className="video-wrap">
                          <VideoPlayer
                            key={video.id}
                            {...{
                              height: 300,
                              // fluid: true,
                              autoplay: false,
                              controls: true,
                              sources: [
                                {
                                  src: video.url,
                                  type: video.mime,
                                },
                              ],
                            }}
                          />
                        </div>
                      )
                    })}
                  </li>
                )
              })}
            </ul>
          )}
        </section>
      </Layout>
    )
  }
  return (
    <Layout location={props.location} title={site.siteMetadata.title}>
      {""}
    </Layout>
  )
}

export default ThinkPage
